.avatar {
  &.avatar-x-small {
    width: 1.5rem;
    height: 1.5rem;
    font-size: 0.5rem;
    line-height: 1.5rem;

    &.avatar-status::after {
      width: 7px;
      height: 7px;
    }
  }

  &.avatar-small {
    width: 2rem;
    height: 2rem;
    font-size: 0.75rem;
    line-height: 2rem;

    &.avatar-status::after {
      width: 10px;
      height: 10px;
    }
  }

  &.avatar-small.avatar-status::after,
  &.avatar-x-small.avatar-status::after {
    @include properties($solid-small-shadow);

    bottom: 0.25px;
    right: 0.25px;
  }

  &.avatar-large {
    width: 6rem;
    height: 6rem;
    font-size: 1.75rem;
    line-height: 6rem;

    &.avatar-status::after {
      width: 18px;
      height: 18px;
      bottom: 5px;
      right: 5px;
    }
  }

  &.avatar-x-large {
    width: 8rem;
    height: 8rem;
    font-size: 2.25rem;
    line-height: 8rem;

    &.avatar-status::after {
      width: 22px;
      height: 22px;
      bottom: 8px;
      right: 8px;

      @include properties($solid-regular-shadow);
    }
  }
}
